.App {
  text-align: center;
  justify-content: left;

}

.App-logo {
  width: 30%;
  min-width:128px;
  margin:5px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@font-face {
  font-family: "DMSansBold";
  src: local("DMSans-Bold.tff"),
  url("./fonts/DMSans-Bold.ttf") format("truetype");
  font-weight: bold;
 }


.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(255, 255, 255);
}

.App-link {
  color: #61dafb;
}

body{
  background-color: rgb(21, 109, 182);
}

#title{
  color: white;
  font-size: x-large;
  text-decoration: underline;
  font-style: "DMSansBold";
}

body {
  padding-bottom: 20px;
}

.navbar {
  margin-bottom: 20px;
}


.button {
  display: inline-block;
  padding: 15px 25px;
  font-size: 24px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color: #000000;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px #999;
  margin: 15px;
}

.button:hover {background-color: #000000}

.button:active {
  background-color: #000000;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

